@import "src/sass/base";

.page {
  @include page;
  justify-content: unset;

  .cards {
    //border: 1px solid aqua;
    //height: 100vh;
    position: relative;
    margin: 90px 15px 15px;
    @media only screen and (max-width: 1124px) {
      margin-top: 110px;
      height: 40vh;
    }
    span {
      @include contentCard;

      &:nth-child(1) {
        top: 0;
        left: 0;
      }

      &:nth-child(2) {
        top: 6px;
        left: 6px;
      }

      &:nth-child(3) {
        top: 12px;
        left: 12px;
      }

      &:nth-child(4) {
        top: 18px;
        left: 18px;
      }

      &:nth-child(5) {
        top: 29px;
        left: 21px;
      }

      &:nth-child(6) {
        @include contentCard;
      }

      &:nth-child(7) {
        top: 2px;
        left: 415px;
        transform: rotate(-45deg);
        @media only screen and (max-width: 820px) {
          visibility: hidden;
        }
      }

      &:nth-child(8) {
        top: -179px;
        left: 515px;
        transform: rotate(-35deg);
        @media only screen and (max-width: 928px) {
          visibility: hidden;
        }
      }

      &:nth-child(9) {
        top: -151px;
        left: 697px;
        transform: rotate(-25deg);
        @media only screen and (max-width: 1119px) {
          visibility: hidden;
        }
      }
    }
  }
}

